import {resetPassword} from '../../utils/api.js'
import md5 from '../../utils/md5.js'
export default {
	data() {
		return {
			loading:false,
			userObj:{
				type:'1',
				userCode:'',
				verCode:'',
				passWord:''
			},
			times:'发送验证码',
			isSend:false,
			seconds:null,
		}
	},
	onLoad() {
		
	},
	methods: {
		submit(){
			if(this.userObj.userCode == '' || this.userObj.userCode.length!=11){
				uni.showToast({
					title: '请输入正确的手机号',
					icon:'none',
					duration: 2000
				})
				return false
			}
			if(this.userObj.verCode == ''){
				uni.showToast({
					title: '请输入短信验证码',
					icon:'none',
					duration: 2000
				})
				return false
			}
			if(this.userObj.passWord == ''){
				uni.showToast({
					title: '请输入账号密码',
					icon:'none',
					duration: 2000
				})
				return false
			}
			this.userObj.openId = this.openID
			this.userObj.passWord = md5.hex_md5(this.userObj.passWord).toUpperCase()   //md5加密转大写
			this.loading = true
			resetPassword({...this.userObj}).then(res =>{
				this.loading = false
				uni.showToast({
				    title: '修改成功',
				    duration: 2000
				});
			}).catch(err =>{
				this.loading = false
				console.log(err)
			})
		},
		sendCode(){
			if(this.isSend) return false
			this.seconds = 60
			let timeOut = setInterval(() =>{
				this.isSend=true
				this.seconds--;
				this.times=`${this.seconds}s后重新发送`
				if(this.seconds==0){
					clearInterval(timeOut)
					this.isSend=false
					this.times="发送验证码"  
				}
			},1000)
		},
	},
	
}
